.sidebar-hide {
  display: none;
  transition: 1s;
}

.sidebar {
  background-color: whitesmoke;
  height: 100%;
  width: 15em;
  display: flex;
  justify-content: center;
  border-top: 10px rgb(255, 255, 255) solid;
  border-bottom: 10px rgb(255, 255, 255) solid;
  border-left: 10px rgb(255, 255, 255) solid;
  z-index: 2;
}

.nav-links {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 0 auto;
  padding: 0.75em;
  font-size: 27px;
  list-style-type: none;
}

.list-item {
  padding-bottom: 0.25em;
}

.list-item {
  float: left;
}

.nav-item {
  color: rgb(0, 0, 0);
  text-decoration: none;
  transition: 0.3s;
  font-family: "Josefin Sans", sans-serif;
  display: flex;
  align-items: center;
}

.nav-item:hover {
  color: rgb(15, 209, 135);
}


#social-container {
  font-family: "Josefin Sans", sans-serif;
  font-size: 20px;
  color: black;
  align-self: flex-start;
}

/* #available-job {
  color: rgb(255, 255, 255);
} */

.sidebar-icon {
  width: 30px;
  padding-right: 10px;
}

#hamburger-toggle {
  font-size: 38px;
  color: black;
  position: absolute;
  top: 0;
  left: 0;
  margin: 20px 0em 0em 20px;
  z-index: 1;
  transition: 0.3s;
  cursor: pointer;
}

#hamburger-toggle:hover {
  color: rgb(15, 209, 135);
}

#sidebar-exit {
  align-self: flex-end;
  margin-bottom: 20px;
  color: rgb(0, 0, 0);
  transition: 0.3s;
  cursor: pointer;
}

#sidebar-exit:hover {
  color: red;
}
